$primary-color: #9B59B6;
$secondary-color: rgb(87, 182, 107);
$background-color: #e0e0e0;
$text-color: #333;

.bgSkills {
    background-color: $background-color; 
    padding: 4em 0;
    text-align: center;
    font-family: $fontB;
}

.skillFrontContainer, .skillAllContainer {
    background-color: #fff;
    width: 80%;
    margin: 2em auto;
    padding: 2em 0;
    border-radius: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    opacity: 0;
    transform: translateY(100px);
    transition: transform 0.6s ease-out, opacity 0.6s ease-out;

    &.show {
        opacity: 1;
        transform: translateY(0);
    }
}

.title {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: $primary-color;
    margin-bottom: 1em;
    font-size: 1.5em;
    font-weight: bold;
    padding: 0 2em;

    .icon {
        width: 150px;
        height: auto;
        margin-right: 20px;
        transition: transform 0.3s ease, box-shadow 0.3s ease;

        &:hover {
            transform: scale(1.15) rotate(3deg);
            box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
        }
    }

    .content {
        flex: 1;
    }
}

.content h1 {
    margin-bottom: 3em;
    margin-right: 6em;
    font-size: 1.5em;
}

.h1Dev {
    color: $secondary-color;
}

.boxes {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.skill-box {
    width: 100%;
    margin: 1em 0;
}

.skill-info {
    display: flex;
    align-items: center;
    width: 100%;
}

.text {
    font-weight: bold;
    color: $text-color;
    flex: 1;
}

.skill-bar {
    width: 40%;
    height: 15px;
    background: #e6e6e6;
    border-radius: 10px;
    overflow: hidden;
    margin-left: 1em;

    &.secondary .skill-progress {
        background: $secondary-color;
    }
}

.skill-progress {
    height: 100%;
    border-radius: 10px;
    background: $primary-color;
    transition: width 1s ease;
}

@media (max-width: 768px) {
    .skillFrontContainer, .skillAllContainer {
        width: 95%;
        padding: 1em;
    }
    
    .title {
        flex-direction: column;
        align-items: center;
        text-align: center;

        .icon {
            width: 100px;
            margin-bottom: 10px;
            margin-right: 0; 
        }
    }

    .content {
        width: 100%; 
    }

    .content h1 {
        margin-bottom: 1em;
        margin-right: 0;
    }

    .boxes {
        align-items: center;
    }

    .skill-box {
        margin: 0.5em 0;
        width: 100%;
    }
    
    .skill-info {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .skill-bar {
        width: 80%;
        margin-left: 0;
        margin-top: 0.5em;
    }
}

@media (max-width: 480px) {
    .title {
        padding: 0 1em;
    }

    .icon {
        width: 80px; 
    }

    .content h1 {
        font-size: 1.2em;
    }

    .skill-bar {
        width: 90%; 
    }
}
