.errorBG {
    background-image: url(../../../public/Media/404.jpg) ;
    height: 100vh;
    color: white;
}
.errorAll {
    text-align: center;
    
}
.return-button {
    margin-top: 3em;
    text-align: center;
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
    background-color: #18d447;
    border: 2px solid #18d447;
    border-radius: 5px;
    transition: background-color 0.3s, border-color 0.3s, color 0.3s;
}

.return-button:hover {
    background-color: #14a340;
    border-color: #14a340;
}

.return-button:focus {
    outline: none;
}

.return-button:active {
    background-color: #0e6e29;
    border-color: #0e6e29;
}
