.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 15px 0;
  color: white;
  z-index: 1000;
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 20px;
}

.container h3 {
  text-align: center;
  margin: 0;
  flex-grow: 1;
  font-family: $fontB;
  font-weight: bold;
  font-size: 30px;
}

.burger-menu {
  display: none;
  font-size: 30px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.nav {
  margin-right: 4em;
}

.nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}

.nav li {
  margin: 0 20px;
  color: white;
  font-family: $fontB;
}

.nav a {
  color: #ffffff;
  position: relative;
  text-decoration: none;
}

.nav a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: #ffffff;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.nav a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.nav.open {
  display: block;
}

.nav.open ul {
  flex-direction: column;
  align-items: center;
  padding: 0; 
}

.nav.open li {
  margin: 10px 0;
  width: 100%; 
}

.nav.open a {
  display: block;
  width: 100%; 
  padding: 10px; 
}

@media (max-width: 768px) {
  .burger-menu {
    display: block;
    position: fixed;
    right: 15px;
    z-index: 1100;
   
  }
  .nav {
    display: none;
    width: 100%;
    text-align: center;
    background-color: #3c3c3ce5;
    position: absolute;
    top: 100%;
    left: 0;
    overflow-x: hidden;
  }
  .nav.open {
    display: flex;
    ul {
      flex-direction: column;
      align-items: center;
    }
    li {
      margin: 10px 0;
    }
  }
  .container h3 {
    font-size: 24px; 
    margin: 0; 
    flex-grow: initial;
  }
}
