.contact {
    font-family: $fontB;
}
.contact h4 {
    text-align: center;
    margin-top: 6em;
    margin-bottom: 4em;
    font-size: 2em;
}

.contactP p {
    text-align: center;
    margin-bottom: 4em;
}

.contactContainer {
    display: flex;
    justify-content: space-between; 
}

.contactMail,
.contactIRL,
.contactPhone {
    width: 30%;
    text-align: center;
    margin: 1.5em;
    border-radius: 10px;
    padding: 1em;
    
}

.contactMail {
    background-color: #9c59b654; 
}

.contactIRL {
    background-color: #3498DB54; 
}

.contactPhone {
    background-color: #e74d3c54; 
}

.contact img {
    width: 4em;
    height: 4em;
    
}

.contactIRLp {
    margin-top: 1.3em;
    margin-bottom: 1.3em;
    font-size: 1.2em;
}

.contactP1 {
    font-weight: bold;
    font-size: 1.5em;
}

@media (max-width: 768px) { 

    .contactContainer {
        flex-direction: column; 
        align-items: center; 
    }
    
    .contactMail,
    .contactIRL,
    .contactPhone {
        width: 80%; 
        margin: 1.5em auto; 
        border-radius: 10px;
        padding: 1em;
        text-align: center; 
    }
}