.containerAbout {
    display: flex;
    flex-wrap: wrap; 
    justify-content: flex-start;
    width: 90%;
    padding-top: 4em;
    padding-left: 2em;
    padding-right: 2em;
    font-family: $fontB;
}

.leftAbout {
    width: 30%; 
}

.pictureAbout {
    margin-right: 20px; 
    border-radius: 5px;
}

.rightAbout {
     width: 60%;
    padding-left: 3em;
    padding-right: 10px;
}

.rightAbout h3 {
   font-size: 250%;
}

.rightAbout p {
    margin-top: 1.2em;
    font-size: 130%;
    line-height: 1.5;
}

.leftAbout p {
    line-height: 1.5;
    margin-top: 1em;
    margin-left: 1em;
}

.leftAbout span {
    font-weight: bold;
}

.buttonAbout {

    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 4em;
    margin-bottom: 4em;
    
}

.pictureAbout img {
    border-radius: 15px;
}

.buttonAbout button {
        transition-duration: 0.4s;
        background-color: #ffffff; 
        border: none;
        color: rgb(0, 0, 0);
        border: 2px solid #555555;
        padding: 16px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        margin: 4px 3em;
        transition-duration: 0.4s;
        cursor: pointer;
        border-radius: 15px;
      
}

.buttonAbout button:hover {
    background-color: #555555;
     color: white;
  }

  
@media (max-width: 768px) {
    .containerAbout {
        flex-direction: column;
        align-items: center;
        padding: 2em 1em;
    }

    .leftAbout {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .pictureAbout {
        text-align: center;
        margin: 0;
    }

    .rightAbout {
        width: 100%;
        padding: 0;
        text-align: center;
        margin-top: 4em;
    }

    .rightAbout h3 {
        font-size: 2em;
    }

    .rightAbout p {
        font-size: 1em;
    }

    .buttonAbout {
        flex-direction: column;
        align-items: center; 
        margin-top: 2em;
    }

    .buttonAbout button {
        width: 80%;
        margin: 10px 0;
    }
}