.Carrousel {
    background-image: url(../../../public/Media/BGC.jpg);
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: $fontB;
    position: relative;
    
}
.Carrousel::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}
.Carrousel-txt-1 {
    margin-top: auto;
    font-size: 2.7em;
    color: rgb(255, 255, 255, 1);
    

}
.Carrousel-txt-1,
.Carrousel-txt-2,
.Carrousel-Item-Icon Img {
    
    position: relative;
    z-index: 1;
}
.Carrousel-txt-1 h3 {
    font-size: 1.2em;
    font-weight: bold;
    color: rgb(255, 255, 255, 1);
    
}

.Carrousel-txt-2 {
    margin-top: 1.5em;
    font-size: 2em;
   
}

.Carrousel-txt-1,
.Carrousel-txt-2 {
    
    align-self: center;
}

.Carrousel-Item-Icon Img {
    width: 5em;
    height: 5em;
    margin-top: 1.2em;
}

.Carrousel-Icon {
    display: flex; 
    align-items: flex-end;
    justify-content: space-between;
    margin-top: auto;
    margin-bottom: 4em;
    text-align: center;
    font-size: larger;
    width: 80%;
    
    
}

.Carrousel-Item-Icon {
    margin: 0 10px; 
    
}

@media (max-width: 768px) {

    .Carrousel {
        height: auto; 
        padding: 10px; 
    }

    .Carrousel-txt-1 {
        font-size: 1.5em; 
        margin-top: 4em;
    }

    .Carrousel-txt-1 h3 {
        font-size: 1em; 
    }

    .Carrousel-txt-2 {
        font-size: 1.2em; 
    }

    .Carrousel-Icon {
        flex-direction: column; 
        align-items: center; 
        margin-bottom: 2em; 
        margin-top: 4em;
    }

    .Carrousel-Item-Icon {
        margin: 10px 0; 
    }

    .Carrousel-Item-Icon img {
        width: 4em; 
        height: 4em; 
    }
}