.projects {
    background-color: $background-color;
    padding: 4em 0;
    text-align: center;
    font-family: $fontB;

    h1 {
        color: $primary-color;
        font-size: 2em;
        margin-bottom: 1em;
    }

    .project-container {
        display: flex;
        justify-content: center;
        gap: 1em;
        flex-wrap: wrap;
        width: 100%;
    }

    .project-box {
        position: relative;
        width: calc(80% - 1em); 
        height: 500px; 
        margin: 0.5em; 
        border-radius: 15px;
        overflow: hidden;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        transition: transform 0.3s ease;

        .button {
            transition-duration: 0.4s;
            background-color: #ffffff; 
            border: none;
            color: rgb(0, 0, 0);
            border: 2px solid #555555;
            padding: 16px 32px;
            text-align: center;
            text-decoration: none;
            display: inline-block;
            font-size: 16px;
            margin: 4px 3em;
            transition-duration: 0.4s;
            cursor: pointer;
            border-radius: 15px;
        }
        .button:hover {
            background-color: #555555;
            color: white;
        }

        &:hover {
            transform: scale(1.05);
        }

        .project-image {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: opacity 0.3s ease;
        }

        .overlay {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 5%; 
            background: white; 
            display: flex;
            justify-content: center;
            align-items: center;
            transition: height 0.3s ease; 

            .text {
                text-align: center; 
                padding: 10px; 
                width: 100%;
                background-color: white;
                border-top: medium rgb(0, 0, 0);
                
                h2 {
                    color: $primary-color;
                    font-size: 1.5em; 
                    margin-bottom: 0.5em;
                    margin-top: 1em; 
                }

                ul {
                    list-style-type: disc; 
                    padding-left: 15px; 
                    font-weight: normal; 
                    width: 70%; 
                    margin: 0 auto; 
                }

                li {
                    margin-bottom: 2em;
                    color: $text-color;
                }
            }
        }

        .bottom-banner {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 20px;
            background-color: white;
        }

        &:hover .overlay {
            height: 90%; /* Hauteur de la zone de texte sur hover */
        }
    }
}

@media (max-width: 768px) {
    .project-box {
        width: calc(100% - 1em); /* Prend toute la largeur de la page, moins l'espace entre */
    }

    h1 {
        font-size: 1.5em; /* Réduction de la taille du titre sur les petits écrans */
    }
}
